//0是未开始，1是预售。2是公售 3是完成
export const STATUS = {
  NOT_START: 0,
  PRE_SALE: 1,
  SALE: 2,
  FINISHED: 3,
};

export const DataStore = {
  state: {
    loading: false,
    totalSupply: 0,
    invitedNum: 0,
    maxSupply: 0,
    status: null,
  },
  checkDataRequest() {
    this.state.loading = true;
  },
  checkDataFinish() {
    this.state.loading = false;
  },
  setTotalSupply(totalSupply) {
    this.state.totalSupply = totalSupply;
  },
  setMaxSupply(maxSupply) {
    this.state.maxSupply = maxSupply;
  },
  setInvitedNum(invitedNum) {
    this.state.invitedNum = invitedNum;
  },
  setStatus(status) {
    this.state.status = status;
  },
  setShowPoster(status) {
    this.showPoster = status;
  },
  clearData() {
    this.state.totalSupply = 0;
    this.state.invitedNum = 0;
    this.state.maxSupply = 0;
    this.state.status = null;
  },
};
