<template>
  <div class="section-content">
    <div class="mint-bg"></div>
    <div class="sale-status"></div>
    <p class="mint-title">{{ title }} Mint</p>
    <p class="mint-restore">
      {{ dataStore.totalSupply }} / {{ dataStore.maxSupply || 0 }}
    </p>
    <div class="form">
      <div class="input-wrapper form-item">
        <input class="input" name="mintAmount" v-model="formData.mintAmount" />
      </div>
      <SingleSelect
        class="form-item"
        name="currency"
        :options="currencyOptions"
        v-model="formData.currency"
      ></SingleSelect>
    </div>

    <template v-if="blockStore.account">
      <button
        v-loading="dataStore.loading"
        class="buy-button"
        :disabled="isChecked || blockStore.config.STOPPED"
        @click="purchase"
      >
        {{ !isChecked ? "Mint" : "Coming Soon" }}
      </button>
    </template>
    <template v-else>
      <button class="buy-button" @click="connect">Connect Wallet</button>
    </template>
  </div>
</template>
<script>
import { DataStore } from "@/store/dataStore";
import SingleSelect from "../components/Select.vue";
import { BlockStore } from "@/store/chainBlock";
import { checkTime } from "@/utils/validate";

export default {
  name: "MintPage",
  components: {
    SingleSelect,
  },
  props: {
    title: {
      default: "SocialFi",
    },
  },
  computed: {
    isChecked() {
      return !checkTime(this.blockStore.config.RELEASE_TIME);
    },
  },
  data() {
    return {
      formData: {
        mintAmount: 0,
        currency: 1,
      },
      currencyOptions: [
        {
          label: "BNB",
          value: 1,
        },
      ],
      blockStore: BlockStore.state,
      dataStore: DataStore.state,
    };
  },
  methods: {
    connect() {
      this.$emit("connect");
    },
    purchase() {
      const {
        WEI_COST: cost,
        GAS_LIMIT: gasLimit,
        CONTRACT_ADDRESS: to,
      } = this.blockStore.config;
      const mintAmount = Number(this.formData.mintAmount);
      if (isNaN(mintAmount)) {
        return this.$message.error("Please input number");
      }
      if (mintAmount < 0.01) {
        return this.$message.error("Minimum mint amount 0.01 BNB");
      }
      const totalCostWei = String(cost * mintAmount);
      const totalGasLimit = String(gasLimit * mintAmount);
      // console.log("Cost: ", totalCostWei);
      // console.log("Gas limit: ", totalGasLimit);
      DataStore.checkDataRequest();
      // this.blockStore.smartContract.methods
      //   .mint(this.blockStore.account, this.mintAmount)
      //   .send({
      //     to,
      //     from: this.blockStore.account,
      //     gasLimit: totalGasLimit,
      //     value: totalCostWei,
      //   })
      this.blockStore.smartContract.methods
        .mint()
        .send({
          to,
          from: this.blockStore.account,
          gasLimit: totalGasLimit,
          value: totalCostWei,
        })
        .once("error", (err) => {
          console.error(err);

          this.$message.warning("Action failed, please retry again");
          DataStore.checkDataFinish();
        })
        .then((receipt) => {
          console.log(receipt);
          this.$message.success("Action was successful");
          DataStore.checkDataFinish();
          this.getData();
        });
    },
  },
};
</script>
