<template>
  <div class="Countdown" align="middle">
    <span class="Countdown-col" v-if="parseInt(countDown.days) > 0">
      <span class="Countdown-col-element">
        <span class="Countdown-col-number">
          {{ addLeadingZeros(countDown.days) }}
        </span>
      </span>
      <span class="time-type">Days</span>
    </span>

    <span class="Countdown-col">
      <span class="Countdown-col-element">
        <span class="Countdown-col-number">
          {{ addLeadingZeros(countDown.hours) }}
        </span>
      </span>
      <span class="time-type">Hours</span>
    </span>
    <span class="Countdown-col">
      <span class="Countdown-col-element">
        <span class="Countdown-col-number">
          {{ addLeadingZeros(countDown.min) }}
        </span>
      </span>
      <span class="time-type">Minutes</span>
    </span>
    <span class="Countdown-col">
      <span class="Countdown-col-element">
        <span class="Countdown-col-number">
          {{ addLeadingZeros(countDown.sec) }}
        </span>
      </span>
      <span class="time-type">Seconds</span>
    </span>
  </div>
</template>
<script>
export default {
  name: "CountDown",
  props: {
    date: {
      default: () => new Date(),
    },
  },
  data() {
    return {
      countDown: {
        days: 0,
        hours: 0,
        min: 0,
        sec: 0,
      },
    };
  },
  watch: {
    date: function (val, oldVal) {
      if (val !== oldVal) {
        this.interval = setInterval(() => {
          const date = this.calculateCountdown(this.date);
          console.log(this.countDown);
          if (!date) {
            this.stop();
          } else {
            this.setCountDown(date);
          }
        }, 1000);
      }
    },
  },
  mounted() {
    this.interval = setInterval(() => {
      const date = this.calculateCountdown(this.date);
      if (!date) {
        this.stop();
      } else {
        this.setCountDown(date);
      }
    }, 1000);
  },
  destroyed() {
    this.stop();
  },
  methods: {
    setCountDown(countObj) {
      for (const key in countObj) {
        if (Object.hasOwnProperty.call(countObj, key)) {
          this.countDown[key] = countObj[key];
        }
      }
    },
    stop() {
      clearInterval(this.interval);
    },
    calculateCountdown(endDate) {
      let diff =
        (Date.parse(new Date(endDate)) - Date.parse(new Date())) / 1000;

      // clear countdown when date is reached
      if (diff <= 0) return false;

      const timeLeft = {
        years: 0,
        days: 0,
        hours: 0,
        min: 0,
        sec: 0,
      };

      // calculate time difference between now and expected date
      if (diff >= 365.25 * 86400) {
        // 365.25 * 24 * 60 * 60
        timeLeft.years = Math.floor(diff / (365.25 * 86400));
        diff -= timeLeft.years * 365.25 * 86400;
      }
      if (diff >= 86400) {
        // 24 * 60 * 60
        timeLeft.days = Math.floor(diff / 86400);
        diff -= timeLeft.days * 86400;
      }
      if (diff >= 3600) {
        // 60 * 60
        timeLeft.hours = Math.floor(diff / 3600);
        diff -= timeLeft.hours * 3600;
      }
      if (diff >= 60) {
        timeLeft.min = Math.floor(diff / 60);
        diff -= timeLeft.min * 60;
      }
      timeLeft.sec = diff;

      return timeLeft;
    },
    addLeadingZeros(value) {
      value = String(value);
      while (value.length < 2) {
        value = "0" + value;
      }
      return value;
    },
  },
};
</script>
