function random(min, max) {
  if (arguments.length < 2) {
    max = min;
    min = 0;
  }

  if (min > max) {
    var hold = max;
    max = min;
    min = hold;
  }

  return Math.floor(Math.random() * (max - min + 1)) + min;
}

function maxOrbit(x, y) {
  var max = Math.max(x, y),
    diameter = Math.round(Math.sqrt(max * max + max * max));
  return diameter / 2;
  //星星移动范围，值越大范围越小，
}

export const Star = function (canvas, canvas2) {
  //画布
  this.canvas2 = canvas2;
  this.ctx = canvas.getContext("2d");
  const w = canvas.width;
  const h = canvas.height;
  this.focalLength = w * 2;
  this.orbitRadius = random(maxOrbit(w, h));
  this.radius = random(60, this.orbitRadius) / 8;
  //星星大小
  this.orbitX = w / 2;
  this.orbitY = h / 2;
  //星星位置
  this.x = Math.random() * w;
  this.y = Math.random() * h;
  this.z = Math.random() * w;
  this.timePass = Math.random() * (2 - 1) + 1;
  //星星移动速度
  this.speed = random(this.orbitRadius) / 50000;
  this.alpha = random(2, 10) / 10;
};

Star.prototype.draw = function (speed = 0) {
  var x = (this.x - this.orbitX) * (this.focalLength / this.z) + this.orbitX,
    y = (this.y - this.orbitX) * (this.focalLength / this.z) + this.orbitY,
    twinkle = random(10);

  if (twinkle === 1 && this.alpha > 0) {
    this.alpha -= 0.05;
  } else if (twinkle === 2 && this.alpha < 1) {
    this.alpha += 0.05;
  }

  this.ctx.globalAlpha = this.alpha;
  this.ctx.drawImage(
    this.canvas2,
    x - this.radius / 2,
    y - this.radius / 2,
    this.radius,
    this.radius
  );

  this.z -= this.timePass + speed;
  if (this.z <= 0) {
    this.z = this.orbitX * 2;
  }
};
