<template>
  <div class="select-wrapper">
    <div class="select-input" :class="{ active: isOpened }" @click="toggle">
      {{ selectLabel }}<span class="arrow"></span>
    </div>
    <div
      class="select-options animate__animated animate__faster"
      :class="{ myFadeInDown: isOpened }"
      v-if="isOpened"
    >
      <div
        class="option"
        :class="{ selected: selectId === option.value }"
        v-for="option in options"
        :key="option.value"
        @click="(e) => choose(e, option.value)"
      >
        {{ option.label }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SingleSelect",
  model: {
    props: "value",
    event: "change",
  },
  data() {
    return { selectId: null, selectLabel: null, isOpened: false };
  },
  props: {
    value: {
      default: null,
    },
    options: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  watch: {
    value: {
      handler() {
        this.selectId = this.value;
        if (this.options && this.options.length > 0) {
          const currSelected = this.options.find(
            (selection) => selection.value === this.selectId
          );
          this.selectLabel = currSelected ? currSelected.label : "";
        }
      },
      immediate: true,
    },
  },
  methods: {
    toggle(event) {
      event.stopPropagation();
      event.preventDefault();
      this.isOpened = !this.isOpened;

      window.addEventListener("click", this.closeHandler);
    },
    choose(event, value) {
      event.stopPropagation();
      event.preventDefault();
      this.selectId = value;
      this.$emit("change", this.selectId);
      this.closeHandler();
    },
    closeHandler() {
      this.isOpened = false;
      window.removeEventListener("click", this.closeHandler);
    },
  },
};
</script>

<style lang="less">
.select-wrapper {
  position: relative;
  width: 100%;
}
.select-input {
  position: relative;
  height: 36px;
  font-size: 12px;
  padding: 8px 12px;
  line-height: 18px;
  color: #fff;
  text-align: left;
  border-bottom: 1px solid #00ffff;
  box-sizing: border-box;
  cursor: pointer;
  &.active {
    .arrow {
      transform: rotate(-180deg);
    }
  }
  .arrow {
    position: absolute;
    right: 10px;
    top: 15px;
    border: 6px solid #19ebff;
    border-bottom-color: transparent;
    border-right-color: transparent;
    border-left-color: transparent;
    transform-origin: 50% 0;
    transition: transform 0.2s linear;
    box-shadow: 0px 2px 23px 0px rgba(0, 255, 255, 0.3);
  }
}
.select-options {
  position: absolute;
  top: 120%;
  left: 0;
  right: 0;
  width: 100%;
  border-radius: 4px;
  background-color: #000;
  border: 1px solid #00ffff;
  z-index: 11;
  .option {
    padding: 8px 10px;
    font-size: 14px;
    color: #fff;
    border-bottom: #00ffff;
    cursor: pointer;
    &.selected {
      background-color: rgba(0, 255, 255, 0.16);
      color: #00ffff;
    }
  }
}
.select-mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: transparent;
  z-index: 10;
}

.myFadeInDown {
  animation: myFadeInDown;
  transform-origin: 50% 0;
}

@keyframes myFadeInDown {
  from {
    opacity: 0;
    -webkit-transform: scaleY(0.2);
    transform: scaleY(0.2);
  }

  to {
    opacity: 1;
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
  }
}
</style>
