<template>
  <div class="home">
    <header>
      <div class="btn-group">
        <template v-if="blockData.account">
          <el-tooltip :content="blockData.account">
            <button
              class="button"
              style="
                max-width: 120px;
                overflow: hidden;
                text-overflow: ellipsis;
                word-break: keep-all;
              "
            >
              {{ blockData.account }}
            </button>
          </el-tooltip>
        </template>
        <template v-else>
          <button class="button" @click="connect">CONNECT WALLET</button>
        </template>
      </div>
    </header>
    <StarsContainer :noBg="true" />
    <div class="section page8">
      <div class="countdownWrapper" v-if="isChecked">
        <div class="title">Start Mint</div>
        <CountDown :date="blockData.config.RELEASE_TIME" />
      </div>
      <MintPage title="Farm" @connect="connect" />
    </div>
  </div>
</template>
<script>
import StarsContainer from "./components/Stars.vue";
import CountDown from "./components/Count.vue";
import MintPage from "./pages/Mint.vue";
import { BlockStore } from "./store/chainBlock";
import { DataStore } from "./store/dataStore";
import { getStore, removeStore, setStore } from "./utils/store";
import Cookies from "js-cookie";
import Web3EthContract from "web3-eth-contract";
import Web3 from "web3";
import Http from "./utils/http";
import { checkTime } from "./utils/validate";

export default {
  name: "MintApp",
  components: { MintPage, StarsContainer, CountDown },
  data() {
    return {
      blockData: BlockStore.state,
      dataStore: DataStore.state,
      switchChange: true,
    };
  },
  computed: {
    isChecked() {
      return !checkTime(this.blockData.config.RELEASE_TIME);
    },
  },
  watch: {
    "blockData.account"(value) {
      if (value) this.getData().then();
    },
  },
  mounted() {
    setInterval(() => {
      this.switchChange = !this.switchChange;
    }, 3000);
    if (getStore({ name: "AUTOPLAY_ENABLED" })) {
      setTimeout(() => {
        this.connect().then();
      }, 500);
    }
  },
  methods: {
    async getData() {
      if (BlockStore.account !== "" && BlockStore.smartContract !== null) {
        // 售卖数
        try {
          DataStore.checkDataRequest();
          const totalSupply = await this.blockData.smartContract.methods
            .totalSupply()
            .call();
          DataStore.setTotalSupply(totalSupply);

          // 最大库存
          const maxSupply = await this.blockData.smartContract.methods
            .maxSupply()
            .call();

          DataStore.setMaxSupply(maxSupply);
          // 状态
          const status = this.blockData.smartContract.methods.state().call();
          DataStore.setStatus(status);

          DataStore.checkDataFinish();
        } catch (error) {
          DataStore.checkDataFinish();
          this.$message.error(error.message);
        }

        try {
          // 邀请人数
          // http://10.1.10.25:1809/v1/invite ? address
          const inviteNum = await Http.post("v1/invite", {
            address: this.blockData.account,
          });
          console.log(inviteNum);
          DataStore.setInvitedNum(inviteNum);
        } catch (err) {
          DataStore.checkDataFinish();
        }
      }
    },
    async connect() {
      if (this.blockData.account) {
        return;
      }
      BlockStore.setLoading(true);

      const abi = await Http.get("/config/SocialFi.json", {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });

      const CONFIG = await Http.get("/config/config.json", {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });

      BlockStore.setConfig(CONFIG);
      // 判断是否安装插件
      const { ethereum } = window;
      const metamaskIsInstalled = ethereum && ethereum.isMetaMask;

      if (metamaskIsInstalled) {
        Web3EthContract.setProvider(ethereum);
        let web3 = new Web3(ethereum);
        try {
          const accounts = await ethereum.request({
            method: "eth_requestAccounts",
          });
          const networkId = await ethereum.request({
            method: "eth_chainId",
          });
          if (networkId == CONFIG.NETWORK.ID) {
            const SmartContractObj = new Web3EthContract(
              abi.abi,
              CONFIG.CONTRACT_ADDRESS
            );

            if (accounts.length === 0) {
              // 请先注册账号
              this.$message.error("Please register wallet account!");
              return false;
            } else if (accounts.length > 1) {
              this.$message({
                message:
                  'Account user may contain many accounts, but first account in the list will be selected as "default" account.',
                type: "warning",
              });
            }

            setStore({ name: "AUTOPLAY_ENABLED", content: true });
            Cookies.set("wallet-address", accounts[0]);

            BlockStore.connectSuccess({
              account: accounts[0],
              smartContract: SmartContractObj,
              web3: web3,
              walletName: "Metamask",
            });
          } else {
            // 请连接正确的网络
            this.$confirm(`Please link the correct network`, "Error", {
              cancelButtonText: "Cancel",
              confirmButtonText: "Retry",
            }).then(() => {
              // 判断浏览器
              this.connect().then();
            });
          }
          this.registerListeners();
        } catch (err) {
          const code = err.code;
          const self = this;
          if (code === 4001) {
            // 用户拒绝了请求
            this.$confirm("User rejects request", "Error", {
              cancelButtonText: "Cancel",
              confirmButtonText: "Retry",
            }).then(() => {
              self.connect().then();
            });
          } else if (code === -32602) {
            // 参数无效
            this.$confirm("Parameter invalid", "Error", {
              cancelButtonText: "Cancel",
              confirmButtonText: "Retry",
            }).then(() => {
              self.connect().then();
            });
          } else if (code === -32002) {
            this.$confirm(
              "Already request wallet. Please open wallet plugin to continue. ",
              "Error",
              {
                cancelButtonText: "Cancel",
                confirmButtonText: "Retry",
              }
            ).then(() => {
              self.connect().then();
            });
          } else {
            // 内部错误
            this.$confirm("Internal error", "Error", {
              cancelButtonText: "Cancel",
            }).then(() => {});
            throw new Error(err);
          }
        }
      } else {
        Cookies.set("wallet-address", "");
        removeStore({ name: "AUTOPLAY_ENABLED" });
        this.$confirm(
          `Haven't installed metaMask plug-in, please install first!`,
          "Error",
          {
            cancelButtonText: "Cancel",
            confirmButtonText: "Download metaMask plug-in",
          }
        ).then(() => {
          // 判断浏览器
          window.open("https://metamask.io/download.html");
        });
      }
    },
    registerListeners() {
      const { ethereum } = window;

      ethereum.on("accountsChanged", this.accountsChange);
      ethereum.on("chainChanged", this.chainChanged);
      ethereum.on("message", this.messageHandler);
      ethereum.on("disconnect", this.disconnect);
    },
    removeListeners() {
      const { ethereum } = window;
      ethereum.removeListener("accountsChanged", this.accountsChange);
      ethereum.removeListener("chainChanged", this.chainChanged);
      ethereum.removeListener("message", this.messageHandler);
      ethereum.removeListener("disconnect", this.disconnect);
    },
    accountsChange(accounts) {
      if (accounts.length > 0) {
        // 切换了钱包账号
        this.$message({
          message:
            'Account user may contain many accounts, but first account in the list will be selected as "default" account.',
          type: "warning",
        });

        Cookies.set("wallet-address", accounts[0]);
        BlockStore.updateAccount({
          account: accounts[0],
          walletName: "Metamask",
        });
      } else {
        // 断开了钱包
        this.$message.error("Wallet link disconnected");
        Cookies.remove("wallet-address");
        BlockStore.disconnect();
        DataStore.clearData();
        setStore({ name: "AUTOPLAY_ENABLED", content: false });
      }
    },
    chainChanged() {
      BlockStore.disconnect();
      DataStore.checkDataRequest();
      Cookies.remove("wallet-address");
      window.location.reload();
    },
    messageHandler(message) {
      console.log(message);
    },
    disconnect() {
      BlockStore.disconnect();
      DataStore.clearData();
    },
  },
};
</script>

