export const BlockStore = {
  state: {
    loading: false,
    account: null,
    smartContract: null,
    web3: null,
    walletName: null,
    errorMsg: "",
    config: {},
  },
  setLoading(isLoading) {
    this.state.loading = isLoading;
  },
  connectFailed() {
    this.state.loading = false;
  },
  updateAccount(params) {
    if (params) {
      const { account, walletName } = params;
      this.state.account = account;
      this.state.walletName = walletName;
    } else {
      this.state.account = null;
      this.state.walletName = null;
    }
  },
  connectSuccess(payload) {
    const { smartContract, web3, account, walletName } = payload;
    this.state.loading = false;
    this.state.smartContract = smartContract;
    this.state.web3 = web3;
    this.state.account = account;
    this.state.walletName = walletName;
  },
  disconnect() {
    this.state.smartContract = "";
    this.state.web3 = "";
    this.state.account = "";
    this.state.walletName = "";
  },
  setConfig(payload) {
    this.state.config = payload;
  },
};
