/**
 * 邮箱
 * @param {*} s
 */
export function isEmail(s) {
  return /^[a-zA-Z0-9]+([-_.][a-zA-Z0-9]+)*@[a-zA-Z0-9]+([-_.][a-zA-Z0-9]+)*\.[a-z]{2,}$/.test(
    s
  );
}

/**
 * 手机号码
 * @param {*} s
 */
export function isMobile(s) {
  return /^1[0-9]{10}$/.test(s);
}

/**
 * 电话号码
 * @param {*} s
 */
export function isPhone(s) {
  return /^([0-9]{3,4}-)?[0-9]{7,8}$/.test(s);
}

/**
 * URL地址
 * @param {*} s
 */
export function isURL(str) {
  const RegUrl = new RegExp();

  RegUrl.compile("^[A-Za-z]+://[A-Za-z0-9-_]+\\.[A-Za-z0-9-_%&?/.=]+$"); // eslint-disable-line
  if (!RegUrl.test(str)) {
    return false;
  }
  return true;
}

/**
 * validate email
 * @param email
 * @returns {boolean}
 */
export function validateEmail(email) {
  const re =
    /^(([^<>()\\[\]\\.,;:\s@"]+(\.[^<>()\\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

  return re.test(email);
}

/**
 * 判断手机号码是否正确
 */
export function isvalidatemobile(phone) {
  const list = [];

  let result = true;

  let msg = "";

  const isPhoneReg = /^0\d{2,3}-?\d{7,8}$/;
  // 增加134 减少|1349[0-9]{7}，增加181,增加145，增加17[678]

  if (!validatenull(phone)) {
    if (phone.length === 11) {
      if (isPhoneReg.test(phone)) {
        msg = window.i18n("admin.address_code_err_msg"); // '手机号码格式不正确';
      } else {
        result = false;
      }
    } else {
      msg = window.i18n("admin.address_code_err_msg"); // '手机号码长度需要为11位';
    }
  } else {
    msg = window.i18n("admin.address_code_err_msg"); // '手机号码不能为空';
  }
  list.push(result);
  list.push(msg);
  return list;
}

/**
 * 判断是否为空
 */
export function validatenull(val) {
  if (typeof val === "boolean") {
    return false;
  }
  if (typeof val === "number") {
    return false;
  }
  if (val instanceof Array) {
    if (val.length === 0) {
      return true;
    }
  } else if (val instanceof Object) {
    if (JSON.stringify(val) === "{}") {
      return true;
    }
  } else {
    if (
      val === "null" ||
      val == null ||
      val === "undefined" ||
      val === undefined ||
      val === ""
    ) {
      return true;
    }
    return false;
  }
  return false;
}

/**
 * 判断是否为空
 */
export function isEmpty(val) {
  return validatenull(val);
}
/**
 * 判断不为空
 */
export function isNotEmpty(val) {
  return !validatenull(val);
}

export function checkTime(time) {
  if (!time) return true;
  // return true;
  let now = new Date().getTime() / 1000;
  let deadline = new Date(time).getTime() / 1000;
  if (now >= deadline) {
    return true;
  }
  return false;
}
